@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
/* import fonts here */

* {
  box-sizing: border-box;
}

:root {
  /* define variables here */
  --green: green;
  --red: red;
  --blue: blue;
}

html {
  touch-action: manipulation;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
}


* {
    font-family: 'Work Sans', sans-serif;
}

.homeFlexContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.navToolsTopLeft {
    display: flex;
    justify-content: flex-start;
    margin-top: 15px;
}

.navToolsTopRight {
    display: flex;
    justify-content: flex-end;
    margin-top: 15px;
}

.registerLink {
    margin-right: 20px;
}

.navToolsTitle {
    display: flex;
    justify-content: center;
    
}

.headerMain {
    border-bottom: 1px solid rgb(186, 186, 186);
    margin-left: 150px;
    margin-right: 150px;
}

.navToolsSub {
    display: flex;
    justify-content: space-around;
    margin-left: 575px;
    margin-right: 575px;
    padding: 30px;
    color: grey;
}

a {
    color: grey;
} 

a {
    position: relative;
}

a:before {
    content: "";
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -3px;
    right: 0;
    background-color: black;
    visibility: hidden;
    transition: all 0.5s ease-in-out;
    
  }

  a:hover:before {
    visibility: visible;
    width: 100%;
  }

  a:hover {
    color: black;
    transition:all 0.7s ease-in-out;
    transition: 0.7s;
  }
.indivProduct {
    display: flex;
    flex-direction: column;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border: 2px solid black;
    padding: 10px;
    margin: 10px;
}

.shop {
    display: flex;
    flex-direction: column;
}

img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}
.splashTitle {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.subTitle {
    display: flex;
    justify-content: center;
    
}

main {
    display: flex;
    justify-content: center;
    height: 500px;
    width: 1250;
    margin-top: 75px;
    -webkit-animation: fadeIn 3s;
            animation: fadeIn 3s; 
}

@-webkit-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }

@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
.cartBox {
    display: flex;
    justify-content: center;
}

.aboutBox {
    display: flex;
    justify-content: center;
}
